
.mint-toast {
  position: fixed;
  max-width: 80%;
  border-radius: 5px;
  background: #58B7FF;
  color: #fff;
  box-sizing: border-box;
  text-align: center;
  z-index: 1000;
  transition: opacity .3s linear
}
.mint-toast.is-placebottom {
  bottom: 50px;
  left: 50%;
  transform: translate(-50%, 0)
}
.mint-toast.is-placemiddle {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%)
}
.mint-toast.is-placetop {
  top: 50px;
  left: 50%;
  transform: translate(-50%, 0)
}
.mint-toast-icon {
  display: block;
  text-align: center;
  font-size: 56px
}
.mint-toast-text {
  font-size: 14px;
  display: block;
  text-align: center
}
.mint-toast-pop-enter, .mint-toast-pop-leave-active {
  opacity: 0
}



